@use '@/scss/abstracts/mixins' as *;
@use '@/scss/abstracts/functions' as *;

.header {
    background-color: #fff;
    padding-block: calc(min(6vw, 5.3rem));

    .container {
        display: flex;
        align-items: center;
    }
    &__logo {
        position: relative;
        z-index: 10;
    }
    nav {
        display: flex;
        justify-content: space-between;
        flex-grow: 1;
    }
    &__list {
        display: flex;
    }
}

@include for-mobile-only {
    .header {

        position: fixed;
        top: 0;
        left: 0;
        width: 100vw;

        .container {
            display: block;
        }

        nav {
            flex-direction: column;
            justify-content: flex-start;
            flex-grow: 0;
        }

        &__list {
            flex-direction: column;
        }
    }

    body {
        padding-top: 5em;
        overflow: hidden;
    }
}