@use '@/scss/abstracts/vars.scss' as *;
@use '@/scss/abstracts/functions.scss' as *;

.btn {
    display: inline-flex;
    font-size: rem(25);
    color: #fff;
    background-color: $cyan;
    padding: em(21, 25) em(88, 25);
    border-radius: 15px;

    &--sharp {
        border-bottom-left-radius: 0;
    }
}