@use '@/scss/abstracts/vars' as *;
@use '@/scss/base/typography.scss' as *;

.header {
    font-size: 25px;
    margin-bottom: 2em;

    &__list {
        align-items: center;
    }

    &__link {
        color: $dark-1;
    }

    &__link-list {
        font-weight: 400;
        gap: 85px;
        margin: 0 auto;
    }

    &__link--active {
        position: relative;
        color: $cyan;
        font-weight: 700;

        &:after {
            position: absolute;
            content: '';
            bottom: -6px;
            left: 0;
            right: 0;
            margin: 0 auto;
            height: 6px;
            width: 60%;
            background-color: #FEC62D;
            border-radius: 21px;
        }
    }
}

.hero {
    margin-bottom: 8em;

    .row {
        flex-direction: row;
        align-items: flex-start;
        gap: 5em;
    }
    &__content {
        max-width: 743px;
    }
    &__title {
        @extend .title;
    }
    &__text {
        color: $dark-1;
        font-size: 30px;
        margin-bottom: 2em;
    }
    &__img {
        position: relative;
        display: flex;
        justify-content: center;
        align-items: center;
    }
    &__content-img {
        position: absolute;
    }
    &__btn {
        font-weight: 700;
    }
}

.intro {
    margin-bottom: 150px;

    text-align: center;
    
    &__title {
        @extend .title;
    }
    &__subtitle {
        color: $dark-1;
        font-size: 30px;
        max-width: 510px;
        margin: 0 auto 70px;
    }
    .row {
        flex-direction: row;
    }
    &__content {
        flex-grow: 1;

        background-color: #CCF0E6;
        padding: 70px 110px;
        text-align: left;
    }
    &__text {
        color: $dark-2;
        font-size: 20px;
        font-weight: 500;
        margin-bottom: 40px;
    }
    &__link {
        position: relative;
        display: flex;
        align-items: center;
        color: $dark-2;
        font-size: 20px;
        font-weight: 700;
        letter-spacing: 3px;
        text-transform: uppercase;
    }
    &__img {
        flex: 0 0 67%;
    }
}

.play-btn {
    position: absolute;
    left: -160px;
    display: block;
    width: 100px;
    height: 100px;
    background-color: #fff;
    border-radius: 50%;
    background-image: url('img/triangle.svg');
    background-repeat:  no-repeat;
    background-position: 55% 50%;
    box-shadow: 0 0 0 14px rgba(#fff, 0.4);
}

.aims {
    margin-bottom: 120px;

    .row {
        flex-direction: row;
        justify-content: flex-start;
        align-items: flex-start;
        gap: 6em;
    }
    &__img {
        flex: 0 0 40%;
        min-height: 650px !important;
    }
    &__content {
        max-width: 700px;
    }
    &__title {
        font-size: 40px;
        font-weight: 700;
        margin-bottom: 50px;
    }
    &__text {
        color: $dark-2;
        font-size: 30px;
        font-weight: 500;
    }
}

.project {
    margin-bottom: 250px;

    &__title {
        @extend .title;
        text-align: center;
    }
    &__subtitle {
        position: relative;
        color: $dark-1;
        font-size: 30px;
        text-align: center;
        @extend .double-underline;
        @extend .double-underline--center;
        margin-bottom: 160px;
    }
    .row {
        flex-direction: row;
        gap: 120px;
        & > * { width: 100% }
    }
}

.project-card {
    display: flex;
    flex-direction: column;

    &__img {
        aspect-ratio: 1;
        border-radius: 50%;
        margin-bottom: 35px;
    }
    &__title {
        color: $cyan;
        font-size: 40px;
        font-weight: 700;
        margin-bottom: 5px;
    }
    &__text {
        color: $dark-1;
        font-size: 20px;
        font-weight: 500;
    }

    &:nth-child(odd) {
        .project-card__title {
            order: 1
        }
        .project-card__text {
            order: 2;
        }
        .project-card__img {
            order: 3;
            border-top-left-radius: 0;
            border-top-right-radius: 0;
            margin: 35px 0 0 0;
        }
    }
}

.testimonial {
    margin-bottom: 240px;

    &__title {
        @extend .title;
        margin-bottom: 20px;
        text-align: center;
    }
    .row {
        flex-direction: row;
    }
    &__sponsors {
        max-width: 1300px;
        margin: 0 auto 60px;
        justify-content: space-around;
        align-items: center;
    }
    &__img {
        flex: 0 0 40%;
        min-height: 700px !important;
    }
    &__content {
        background-color: #CCF0E6;
        padding: 50px 135px;
    }
    &__stats {
        justify-content: space-between;
        margin-bottom: 123px;
    }
    &__stats-item {
        color: $cyan;
            width: 100%;

        h2 {
            font-size: 70px;
            font-weight: 800;
        }
        p {
            font-size: 30px;
            font-weight: 600;
        }
    }
    &__quote {
        color: $dark-2;
        font-size: 25px;
        font-weight: 500;
        margin-bottom: 2em;
    }
    &__quote-author {
        color: $cyan;
        font-size: 25px;
        font-style: italic;
        font-weight: 500;
    }
}


.contact {

    margin-bottom: 240px;

    .row {
        flex-direction: row;
        align-items: flex-start;
        gap: 3em;
    }
    &__title {
        margin-top: 0;
        @extend .title;
    }
    &__text {
        position: relative;
        color: $dark-2;
        font-size: 25px;
        font-weight: 500;
        @extend .double-underline;
    }
    &__form {
        border: 1px solid $dark-2;
        flex: 0 0 70%;
        padding: 35px 54px;
        display: flex;
        flex-direction: column;

        input {
            font-size: 25px;
            font-weight: 500;

            margin-bottom: 30px;
            padding-block: 25px;
            border-bottom: 1px solid #000;
        }
        button {
            align-self: flex-end;
            margin-top: 1em;
        }
    }
}