@import url('https://fonts.googleapis.com/css2?family=Montserrat&family=Poppins:wght@400;500;700;800&display=swap');

body {
    font-family: 'Poppins', serif;
    line-height: 1.5;
}

.title {
    font-size: 70px;
    font-weight: 800;
    line-height: 1.5;
}

.double-underline {
    &:before, &:after {
        position: absolute;
        content: '';
        height: 5px;
        background-color: $cyan;
        border-radius: 15px;
        width: 135px;
        left: 0;
    }
    &:before {
        bottom: -35px;
    }
    &:after {
        bottom: -50px;
    }

    &--center {
        &:before, &:after {
            margin: 0 auto;
            right: 0;
        }
    }
}